@import "../../styles/helpers.scss";

.hero {
    background: url("../../assets/images/pattern.svg") no-repeat center,
        linear-gradient(180deg, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 5e-05) 49.88%, rgba(0, 0, 0, 0.5) 100%);
    background-size: cover;
    background-blend-mode: overlay;
    display: flex;
    flex-flow: column;
    justify-content: center;
    min-height: 100vh;

    @media screen and (max-width: 768px) {
        padding-bottom: 30px;
        padding-top: 30px;
    }

    &__title {
        font-size: 36px;
        line-height: 42px;
        text-align: center;
        font-weight: 100;
        color: $color-text-light;
        text-shadow: 0 1px 1px #000;
        margin: 0;
        position: relative;
        top: 0;
        padding: 0 0 25px 0;
    }

    &__variants {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        row-gap: 40px;
        column-gap: 80px;

        @media screen and (max-width: 768px) {
            row-gap: 20px;
            column-gap: 40px;
        }
    }
}