@import "../../styles/helpers.scss";

.package {
    position: relative;
    overflow: hidden;
    max-width: 320px;

    &::before {
        content: "";
        position: absolute;
        left: -44px;
        width: 73px;
        top: -50px;
        height: 73px;
        transform: rotate(45deg);
        background: none;
        border: 4px solid $color-default;
        z-index: 2;
    }

    &--selected {

        &::before {
            border: 4px solid $color-selected;
        }

        .package__main-info {
            border-left: 4px solid $color-selected;
            border-right: 4px solid $color-selected;
            border-bottom: 4px solid $color-selected;
        }

        .package__description {
            border-right: 4px solid $color-selected;
            border-top: 4px solid $color-selected;
        }

        .package__weight-wrapper {
            background-color: $color-selected;
        }
    }

    &--disabled {

        &::before {
            border: 4px solid $color-label-disabled;
        }

        .package__weight-wrapper {
            background-color: $color-label-disabled;
        }

        .package__main-info {
            border-left: 4px solid $color-label-disabled;
            border-right: 4px solid $color-label-disabled;
            border-bottom: 4px solid $color-label-disabled;

            &::after {
                content: '';
                position: absolute;
                display: block;
                width: 100%;
                top: -37px;
                left: 0;
                right: 0;
                bottom: 0;
                clip-path: polygon(10% 0, 100% 1%, 100% 100%, 0 100%, 0 7%);
                background-color: rgba(255, 255, 255, 0.55);
                z-index: 2;
            }
        }

        .package__description {
            border-right: 4px solid $color-label-disabled;
            border-top: 4px solid $color-label-disabled;
        }
    }

    &__main-info {
        background-color: $color-card-background;
        border-left: 4px solid $color-default;
        border-right: 4px solid $color-default;
        border-bottom: 4px solid $color-default;
        border-bottom-left-radius: 12px;
        border-bottom-right-radius: 12px;
        position: relative;
        top: -2px;
        z-index: 1;
    }

    &__description {
        font-size: 16px;
        line-height: 19px;
        font-weight: 400;
        font-family: $trebuchet;
        color: $color-text-grey;
        background-color: $color-card-background;
        position: relative;
        padding: 15px 48px 3px 15px;
        border-right: 4px solid $color-default;
        border-top: 4px solid $color-default;
        border-top-right-radius: 12px;
        margin-left: 39px;
        margin-bottom: 0;
        margin-top: 0;
        z-index: 1;

        &::before {
            content: "";
            position: absolute;
            left: -31px;
            width: 61px;
            top: 12px;
            height: 62px;
            transform: rotate(45deg);
            background-color: $color-card-background;
            z-index: -1;

        }
    }

    &__description--hover {
        color: $color-selected;
    }

    &__title {
        font-size: 48px;
        line-height: 56px;
        font-weight: 700;
        font-family: $trebuchet;
        color: $color-text-dark;
        letter-spacing: 0.7px;
        margin: 0 0 15px 0;
        padding: 0 48px;
    }

    &__sort {
        font-size: 24px;
        line-height: 26px;
        display: block;
        font-weight: 700;
        letter-spacing: 0;
    }

    &__details {
        padding: 0 48px;
        margin-bottom: 25px;
    }

    &__detail {
        font-size: 14px;
        line-height: 16px;
        font-weight: 400;
        font-family: $trebuchet;
        color: $color-text-grey;
        display: block;
        margin: 0;
    }

    &__quantity {
        font-weight: 700;
    }

    &__weight-wrapper {
        background-color: $color-default;
        border-radius: 50%;
        width: 80px;
        height: 80px;
        display: flex;
        flex-flow: column;
        row-gap: 6px;
        justify-content: center;
        align-items: center;
        position: absolute;
        margin: 16px;
        bottom: 0;
        right: 0;
    }

    &__weight-number {
        color: $color-text-light;
        font-size: 42px;
        line-height: 22px;
        text-align: center;
        margin-top: 10px;
        font-family: $trebuchet;
    }

    &__weight-type {
        color: $color-text-light;
        font-size: 21px;
        line-height: 22px;
        text-align: center;
        font-family: $trebuchet;
    }

    &__cover-wrapper {
        position: relative;
        bottom: -4px;
    }

    &__cover-image {
        border-bottom-left-radius: 9px;
    }

    &__comment {
        font-family: $trebuchet;
        font-weight: 400;
        font-size: 13px;
        color: $color-text-light;
        text-align: center;
        margin-bottom: 0;

        &--disabled {
            color: $color-text-disabled;
        }
    }

    &__comment-link {
        color: $color-default;
        position: relative;

        &::after {
            content: '';
            display: block;
            position: absolute;
            width: 100%;
            right: 0;
            border-bottom: 1px dashed $color-default;
        }
    }
}